.list-item {
    display: grid;
    height: 1.8em;
    grid-template-columns: auto 1fr auto;
    column-gap: 1em;
    padding: 16px;    
    border: 1px solid #FFFFFF;
    border-radius: 8px;
    align-items: center;    
}

.item-item-disabled {
    background-color: rgba(255,255,255,0.3);
}

.list-item-left {
    display: grid;
}

.list-item-middle {
    display: grid;
}

.list-item-right {
    display: grid;
}