@import url('https://fonts.googleapis.com/css?family=Roboto+Mono:regular,bold,italic&subset=latin,latin-ext');

body {
  margin: 0;
  background: linear-gradient(#1E9DE4, #E2BE77);
  font-family: Roboto Mono;
  font-style: normal;
  color: white;
}

.page {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 100vh;
  grid-template-columns: 1fr;
}

.title {
  font-weight: 900;
  font-size: xx-large;
  text-align: center;
}

.footer {
  text-align: center;
}

.list-items {
  display: grid;
  justify-content: center;
  grid-template-columns: 0.5fr;
  padding: 2em 0em;
  row-gap: 1em;
}

.add {
  display: grid;
  height: 100%;
  justify-content: center;
}